
@import "../_variable.less";
@import "../_function.less";

@list-hover: @darkHover;
@list-selected: @darkSelected;
@list-active: @darkActive;
@list-hover-dark-bg: @lightHover;
@list-selected-dark-bg: @lightSelected;
@list-active-dark-bg: @lightActive;

//status rules
.bw-hflex{
	width: 100%;
	//user could override to auto if he want to auto-scroll bar when content is large the parent
	overflow-x: hidden;
}
.bw-vflex{
	height:100%;
	//user could override to auto if he want to auto-scroll bar when content is large the parent
	overflow-y: hidden;
}

//widget rules
.bww-checkbox{
	.box-sizing;
	display: flex;
	align-items: center;
	> input {
		cursor: pointer;
		&[disabled]{
			cursor: default;
		}
	}
	> span{
		padding-left:4px;
	}
	&.bw-disabled{
		> span{
			color: @disabledText;
		}
	}
}


.bww-list{
	.box-sizing;
	overflow-y:auto;
	> ul{
		.box-sizing;
		padding:0;
		position:relative;
		list-style: none;
		> li {
			.box-sizing;
			position:relative;
			padding:3px;
			cursor: pointer;
			&:hover{
				background-color: @list-hover;
			}
			&.bw-selected{
				background-color: @list-selected
			}
			&:active{
				background-color: @list-active;
			}
		}
	}
	&.bw-disabled{
		> ul{
			> li {
				cursor:default;
				color: @disabledText;
				&:hover,:active{
					background-color:inherit;
				}
				&.bw-selected{
					background-color: @list-selected
				}
			}
		}
	}
	&.dark-bg > ul > li{
		&:hover{
			background-color: @list-hover-dark-bg;
		}
		&.bw-selected{
			background-color: @list-selected-dark-bg
		}
		&:active{
			background-color: @list-active-dark-bg;
		}
	}
}

